'use client'
import { useRouter } from "next/navigation";
import SellYourProduct from "../components/sell-your-product/SellYourProduct";
import { useEffect } from "react";
import { AuthProvider } from "@/context/authContextSeller";


export default function CategoriesPage() {
  const router = useRouter();

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/seller/check-auth", {
          credentials: "include",
        });
        if (!res.ok) throw new Error();
      } catch {
        router.push("/Sell-log-in"); // Redirect if not authenticated
      }
    };

    checkAuth();
  }, [router]);
  return (
    <div>
       <AuthProvider>
      <SellYourProduct />
      </AuthProvider>
    </div>
  )
}
