"use client";

import { createContext, useContext, useEffect, useState } from "react";

// Define context type
type SellerAuthContextType = {
  seller: { name: string; email: string } | null;
  setSeller: (seller: { name: string; email: string } | null) => void;
  logoutSeller: () => void;
};

// Create Context
const SellerAuthContext = createContext<SellerAuthContextType | undefined>(undefined);

export function SellerAuthProvider({ children }: { children: React.ReactNode }) {
  const [seller, setSeller] = useState<{ name: string; email: string } | null>(null);

  useEffect(() => {
    const fetchSeller = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/seller/check-auth", {
          credentials: "include",
        });

        if (!res.ok) throw new Error("Not authenticated");

        const data = await res.json();
        setSeller(data.seller);
      } catch {
        setSeller(null);
      }
    };

    fetchSeller();
  }, []);

  const logoutSeller = async () => {
    await fetch("http://localhost:5000/api/seller/logout", {
      method: "POST",
      credentials: "include",
    });

    setSeller(null);
  };

  return (
    <SellerAuthContext.Provider value={{ seller, setSeller, logoutSeller }}>
      {children}
    </SellerAuthContext.Provider>
  );
}

// Custom Hook
export function useSellerAuth() {
  const context = useContext(SellerAuthContext);
  if (!context) {
    throw new Error("useSellerAuth must be used within a SellerAuthProvider");
  }
  return context;
}
