"use client";

import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";

export default function Navbar() {
  const router = useRouter();
  const [seller, setSeller] = useState<{ name: string; email: string } | null>(null);

  // Fetch Seller Info
  useEffect(() => {
    const fetchSellerInfo = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/seller/check-auth", {
          credentials: "include", // Send cookies
        });

        if (!res.ok) throw new Error("Not authenticated");

        const data = await res.json();
        setSeller(data.seller); // Store seller info
      } catch {
        setSeller(null);
      }
    };

    fetchSellerInfo();
  }, []);

  // Handle Logout
  const handleLogout = async () => {
    await fetch("http://localhost:5000/api/seller/logout", {
      method: "POST",
      credentials: "include",
    });

    setSeller(null); // Clear seller state
    router.push("/Sell-log-in");
  };

  return (
    <nav className="bg-white shadow-md">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          {/* Logo */}
          <div className="flex-shrink-0 flex items-center">
            <img className="h-8 w-auto" src="/logo.svg" alt="Logo" />
          </div>

          {/* Navigation */}
          <div className="flex items-center space-x-8">
            {seller ? (
              <div className="flex items-center space-x-4">
                <span className="text-gray-700 font-medium">{seller.name}</span>
                <button
                  onClick={handleLogout}
                  className="bg-red-500 text-white font-bold py-2 px-4 rounded"
                >
                  Logout
                </button>
              </div>
            ) : (
              <button
                onClick={() => router.push("/Sell-log-in")}
                className="bg-green-500 text-white font-bold py-2 px-4 rounded"
              >
                Login
              </button>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}
